<template>
    <div class="box">
        <div class="box-header with-border">
            <h3 class="box-title">Danh sách banner</h3>
        </div>
        <div class="box-body">
            <router-link :to="{name: 'route.admin.banner.add'}" class="btn btn-success">Thêm banner</router-link>
            <table class="table table-hover" id="table-banner">
                <tr>
                    <th>Tên</th>
                    <th>Url</th>
                    <th>Hình</th>
                    <th style="width:75px;">Trạng thái</th>
                    <th style="width:107px;">Chức năng</th>
                </tr>
                <tr v-for="(item, index) in banner" :key="item.id">
                    <td>{{ item.name }}</td>
                    <td>{{ item.url }}</td>
                    <td style="width:700px;">
                        <img width="100%" :src="api+'/image/banner/'+item.image">
                    </td>
                    <td v-on:click="updateStatus(index)">
                        <button v-if="item.status==1" class="btn btn-success">Bật</button>
                        <button v-if="item.status!=1" class="btn btn-danger">Tắt</button>
                    </td>
                    <td>
                        <router-link :to="{name: 'route.admin.banner.edit', params:{id: item.id}}" class="btn btn-default">Sửa</router-link>
                        <button v-on:click="deleteBanner(index)" class="btn btn-danger">Xóa</button>
                    </td>
                </tr>
            </table>
        </div>
        <!-- /.box-body -->
    </div>
</template>

<script>
import axios from "axios";

export default {
    metaInfo: {
        title: "Danh sách banner"
    },
    data() {
        return {
            api: this.$store.state.domainApi,
            banner: []
        }
    },
    methods: {
        getDataBanner(){
            axios.get(this.api + "/admin/banner",{
                headers:{
                    Authorization: `Bearer ${localStorage.token}`
                }
            })
            .then(response=>{
                this.banner = response.data;
            })
        },
        updateStatus(index){
            var item = this.banner[index];
            var status = item.status == 0 ? 1 : 0;
            axios.put(this.api + "/admin/banner",{
                id: item.id,
                status: status
            },{
                headers:{
                    Authorization: `Bearer ${localStorage.token}`
                }
            })
            .then(response=>{
                if(response.data.success) this.banner[index].status = status;
                else this.flash(response.data.message, "error", {timeout: 3000});
            });
        },
        deleteBanner(index){
            this.$swal.fire({
                title: 'Bạn có muốn xóa banner này không',
                text: "",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                cancelButtonText: 'Không !',
                confirmButtonText: 'Có !'
            }).then((result) => {
                if (result.value) {
                    axios.delete(`${this.api}/admin/banner`,{
                        headers: {
                            Authorization: `Bearer ${localStorage.token}`
                        },
                        params:{
                            id: this.banner[index].id
                        }
                    })
                    .then(response => {
                        if(response.data.success===1){
                            this.banner.splice(index, 1);
                            this.flash(response.data.message, 'success', {
                                timeout: 3000
                            });
                        }else{
                            this.flash(response.data.message, 'error', {
                                timeout: 3000
                            });
                        }
                    })
                    .catch(e => {
                        console.log(e);
                    });
                }
            });
        }
    },
    created() {
        this.getDataBanner();   
    }
}
</script>

<style scoped>
#table-banner th{
    border: 1px solid #ecf0f5;
    padding: 5px;
}
#table-banner td{
    border: 1px solid #ecf0f5;
    padding: 5px;
}
</style>